import React from 'react';
import lifeExpectancy from '../json/lifeExpectancy';
function Table(props) {
  const dateOfBirth = props.store.getState().inputs.date;
  const countryCode = props.store.getState().inputs.countryCode;
  const ageData = lifeExpectancy(countryCode)[0]
  const gender = props.store.getState().inputs.gender;
  const renderTable = ()=>{
    const timeFromUnix = Math.round(new Date(dateOfBirth).getTime()/1000/60/60/24/7/4);
    const timeFromBirth = Math.round(Date.now()/1000/60/60/24/7/4) - timeFromUnix;
    const diffOfTime = (Date.now() - new Date(dateOfBirth).getTime());
    const diffYears = new Date(diffOfTime).getFullYear()-1970;

    const timeExpected = Math.round((Number(ageData[gender])*365)/7/4);
    
    var ret = [];
    for(let i=0;i<timeExpected;i++){
      if(i<timeFromBirth){
        ret.push(<span key={i} className="circle dotted"></span>);
      }else{
        ret.push(<span key={i} className="circle"></span>);
      }
    }
    ret.push(<p key="3001" className="isFrom">{Math.round(diffYears)}/{Math.round((Number(ageData[gender])*365)/7/4/12)} years</p>);
    ret.push(<p key="3002" className="percent">{Math.round((100/timeExpected*timeFromBirth)*100)/100}%</p>);
    return ret;
  }
  return (
    <div className="Table">
        {renderTable()}
    </div>
  );
}

export default Table;
