import React from 'react';
import Header from './Header'   
import Inputs from './Inputs'   
import Table from './Table'   
import Footer from './Footer'   

function Page(props) {
  return (
    <div className="Page">
        <Header/>
        <Inputs store={props.store}/>
        <Table store={props.store}/>
        <Footer/>
    </div>
  );
}

export default Page;
